/* eslint-disable no-console */
/* eslint-disable no-unused-vars */
import TokenService from "../services/TokenService";
import { stepjobApi } from "../api/service"
import Vue from "vue";
import router from "../router";

export default {
  namespaced: true,
  state: {
    currentUser: {},
  },

  getters: {
     getEmployer(state) {
      if (!state.currentUser.id) {
        router.push("/login");
      }

      return state.currentUser;
    },
  },

  actions: {
    logoutEmployer({ commit }) {
      return commit('logoutEmployer');
    },
    async getUserInformation({ commit }) {
      if (!TokenService.getToken()) {
        return router.push({ name: 'login' });
      }

      const user = await stepjobApi.get('employers/me');

      commit('setUser', user);
      return user;
    },
    async login({ commit }, { email, password }) {
      try {
        const data = {
          email,
          password,
        };
        const response = await stepjobApi.post('/employers/login', data);

        if (response.headers && response.headers.authorization) {
          TokenService.saveToken(response.headers.authorization)
          Vue.$toast.warning('Baaryla Giri yapld', { duration: 3000 });
          commit('setUser', response.data);
          router.push("/dashboard");
        } else {
          Vue.$toast.error(response.data.message, { duration: 7000 });
        }

      } catch (error) {
        throw error;
      }
    },
    async changePassword({ commit }, { newPassword, newPasswordAgain, oldPassword }) {
      try {
        const data = {
          newPassword,
          newPasswordAgain,
          oldPassword
        };
        await stepjobApi.post('/employers/change-my-password', data);
        Vue.$toast.success('ifreniz deitirildi.', { duration: 5000 });
      } catch (error) {
        throw error;
      }
    }
  },
  mutations: {
    logoutEmployer(state) {
      state.currentUser = {};
    },
    setUser(state, user) {
      state.currentUser = user;
    },
  },
};
