<template>
    <div class="toaster-wrapper">
        <transition-group name="fade">
            <div
                v-for="item in reversedItems"
                :key="item.id"
                :class="[item.variant.className]"
                class="toaster"
                @click="!item.closeOnClick || onClose(item)">
                <div :class="[`${item.variant.className}_header`, 'header']">
                    <div class="title">{{ item.title }}</div>
                    <div
                        class="close"
                        @click="onClose(item)">
                        
                    </div>
                </div>
                <component
                    :is="item.message"
                    v-if="typeof item.message === 'object'"
                    v-bind="item.props"
                    class="content" />
                <div
                    v-else
                    class="content">
                    {{ item.message }}
                </div>
                <div
                    v-if="item.duration.autoClose"
                    class="duration-wrapper">
                    <div
                        class="duration"
                        :class="item.duration.classes"
                        :style="item.duration.style" />
                </div>
            </div>
        </transition-group>
    </div>
</template>

<script>
export default {
    name: 'Toaster',

    data() {
        return {
            items: [],
            id: 0,
            config: {
                duration: 2000,
                variants: {
                    error: {
                        className: 'error',
                        title: 'Hata',
                    },
                    success: {
                        className: 'success',
                        title: 'Baarl',
                    },
                    warning: {
                        className: 'warning',
                        title: 'Uyar',
                    },
                    info: {
                        className: 'info',
                        title: 'Bilgilendirme',
                    },
                },
            },
        };
    },

    computed: {
        reversedItems() {
            return this.items.slice().reverse();
        },
    },

    beforeDestroy() {
        this.items.forEach((item) => {
            if (item.duration.onCloseId) {
                clearTimeout(item.duration.onCloseId);
            }
        });
    },

    methods: {
        toast(message, config = {}) {
            const { variants } = this.config;
            const variant = variants[config.variant] || variants.success;
            const title = config.title || variant.title;
            const id = config.id || this.id++;
            const { unique } = config;
            const group = config.group || 'default';
            const duration = config.duration || this.config.duration;
            const autoClose = config.autoClose || config.duration !== undefined || false;
            const closeOnClick = config.closeOnClick !== false;
            const props = config.props || null;

            if (unique && this.items.some(item => group !== 'default' && item.group === group)) {
                return;
            }

            const item = {
                id,
                message,
                title,
                variant,
                unique,
                group,
                duration: {
                    seconds: duration,
                    autoClose,
                },
                closeOnClick,
                props,
            };


            this.items.push(item);

            if (autoClose) {
                const lastIndex = this.items.length - 1;

                item.duration.onCloseId = setTimeout(() => {
                    this.onClose(item);
                }, duration);

                item.duration.style = {
                    transition: `width ${duration / 1000}s`,
                };

                setTimeout(() => {
                    item.duration.classes = ['hide'];

                    this.$set(this.items, lastIndex, item);
                });
            }
        },

        onClose(closedItem) {
            this.items = this.items.filter(item => item.id !== closedItem.id);
        },

        hideAll() {
            this.items = [];
        },
    },
};

</script>

<style lang="scss" scoped>
.toaster-wrapper {
    position: fixed;
    right: 10px;
    top: 10px;
    z-index: 999999999;

    .toaster {
        width: 325px;
        min-height: 80px;
        opacity: 0.9;
        border-radius: 5px;
        color: white;
        margin-top: 10px;


        .header {
            display: flex;
            justify-content: space-between;
            font-weight: bold;
            padding: 7px 5px 2px 0;
            margin: 0 10px;
            font-size: 15px;

            .close {
                cursor: pointer;
                margin-left: 5px;
                position: relative;
                top: -6px;
                right: -5px;
                font-size: 19px;
                color: white;
            }
        }

        .content {
            padding: 15px 10px 20px 10px;
        }

        &.error {
            background-color: #ea5f5f;
            .duration-wrapper .duration {
                border-bottom: 2px solid lighten(#ea5f5f, 10%);
            }
        }

        &.success {
            background-color: #00d563;
            .duration-wrapper .duration {
                border-bottom: 2px solid lighten(#00d563, 20%);
            }
        }

        &.warning {
            background-color: #ffaa00;
            .duration-wrapper .duration {
                border-bottom: 2px solid lighten(#ffaa00, 20%);
            }
        }

        &.info {
            background-color: #26c0cd;
            .duration-wrapper .duration {
                border-bottom: 2px solid lighten(#26c0cd, 20%);
            }
        }

        .duration-wrapper {
            margin: 0 10px;
            padding-bottom: 5px;

            .duration {
                border-radius: 10px;
                width: 100%;

                &.hide {
                    width: 0;
                }
            }
        }

    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .2s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
}
</style>
