import Toaster from '../components/Toaster';

export default {
    install(Vue, options) {
        const Constr = Vue.extend(Toaster);
        const VueToaster = new Constr();
        const vm = VueToaster.$mount();

        VueToaster.config = Object.assign(VueToaster.config, options);

        document.querySelector('body').appendChild(vm.$el);

        Vue.$toast = {
            hide(id) { VueToaster.onClose({ id }); },
            hideAll() { VueToaster.hideAll(); },

            success(message = 'leminiz baaryla gerekletirildi', toasterOptions = {duration: 10000}) {
                const toastOptions = {
                    ...toasterOptions,
                    variant: 'success',
                };
                VueToaster.toast(message, toastOptions);
            },

            error(message = 'leminiz srasnda bir hata olutu.', toasterOptions = {duration: 10000}) {
                const toastOptions = {
                    unique: true,
                    // group: 'error',
                    ...toasterOptions,
                    variant: 'error',
                };
                VueToaster.toast(message, toastOptions);
            },

            warning(message, toasterOptions = {}) {
                const toastOptions = {
                    ...toasterOptions,
                    variant: 'warning',
                };
                VueToaster.toast(message, toastOptions);
            },

            info(message, toasterOptions = {}) {
                const toastOptions = {
                    ...toasterOptions,
                    variant: 'info',
                };
                VueToaster.toast(message, toastOptions);
            },
        };

        Vue.prototype.$toast = Vue.$toast;
    },
};
