export async function getActivityArea() {
  return await stepjobApi.get('/activity-areas');
}

export async function register(model) {
  await stepjobApi.post('/employers/register', {
    activityAreaId: model.activity.id,
    email: model.email,
    organizationName: model.organizationName,
    password: model.password
  });
  Vue.$toast.success('Baaryla Kayt oldunuz!', {duration: 5000});
}

export async function forgetMyPassword(email) {
  return await stepjobApi.post(`/employers/forget-my-password?email=${email}`);
}

export async function forgetMyPasswordCode(model) {
  return await stepjobApi.post('/employers/forget-my-password/change-password',
      {
        activationCode :model.forgetCode,
        email : model.forgetEmail,
        password: model.newPassword
      });
}

export async function updateProfileEmployer(model) {
  await stepjobApi.post(`/employers/me/update-profile`,
      {
        activityAreaId: model.activity.id,
        organizationName: model.organizationName,
        webSite: model.webSite,
        twitterUsername: model.twitterUsername,
        instagramUsername: model.instagramUsername,
        facebookUsername: model.facebookUsername,
        about: model.about
      });
  Vue.$toast.success('Profiliniz gncellendi',{duration: 3000});
}
export async function updateProfilePicture(file) {
  await stepjobApi.post('/employers/me/update-profile-picture',
    file
  );
  Vue.$toast.success('Profil fotorafnz gncellendi',{duration: 3000});
}
