/* eslint-disable no-console */
import Vue from 'vue';
import TokenService from './TokenService';

const statusResponseHandlerConfig = {
    401: (response) => {
        // TokenService.removeToken();
        //
        // if (response.config.url.indexOf('identity/signin') > -1) {
        //     return false;
        // }
        //
        // Vue.$toast.error('Bir hata olutu, ltfen tekrar giri yapnz.');
    },
    404: (error) => {
        if (window.location.pathname.includes('forgetPassword')) {
            Vue.$toast.error(error.data.message);
        }
    },
};

export default class Interceptor {
    constructor(axiosObject) {
        this.requestInterceptor = null;
        this.responseInterceptor = null;
        this.axiosObject = axiosObject;
    }

    setRequestInterceptor() {
        this.requestInterceptor = this.axiosObject.interceptors.request.use((config) => {
            const authToken = TokenService.getToken();

            if (authToken) {
                config.headers.common.Authorization = `Bearer ${authToken}`;
            }

            return config;
        }, error => Promise.reject(error));
    }

    setResponseInterceptor() {
        this.responseInterceptor = this.axiosObject.interceptors.response.use(
            response => response,
            ({ response }) => {
                if (response && response.status < 500) {
                    if (statusResponseHandlerConfig[response.status]) {
                        statusResponseHandlerConfig[response.status](response);
                        return Promise.reject(response);
                    }

                    const { message } = response.data;
                    Vue.$toast.error(message || response.data.errors.join(' '));
                } else {
                    Vue.$toast.error();
                }
                return Promise.reject(response);
            },
        );
    }
}
